import React, { Fragment, memo } from 'react'

import { useHistory } from 'react-router-dom';

import { Card, CardBody, Col, FormFeedback, Input, Row, Form } from 'reactstrap'

import { DatePicker, Select, Switch } from 'antd'

import * as ReactFeather from 'react-feather';

import { ReactComponent as GenderIcon } from '@src/assets/images/icons/gender.svg';
import { ReactComponent as BirthdayCake } from '@src/assets/images/icons/birthday-cake.svg';

import classNames from 'classnames';

import FilterService from "@src/services/filter";

import CustomerService from '@src/services/customerInfo';

import { validateEmail } from '@src/helper/common';

import { injectIntl } from 'react-intl';

import { useForm, Controller } from 'react-hook-form';

import * as styles from '../styles/CustomerForm.module.scss'

function CustomerForm({
  customerInfoId,
  userData,
  onSubmit,
  titleText,
  submitText,
  intl
}) {
  const [provinces, setProvinces] = React.useState([]);
  const [districts, setDistricts] = React.useState([]);
  const [wards, setWards] = React.useState([]);
  const history = useHistory();

  const { handleSubmit, control, reset, watch, formState: { errors }, setValue } = useForm({
    defaultValues: {
      customerFullName: '',
      customerPhone: '',
      customerEmail: '',
      dob: null,
      male: true,
      customerAddress: '',
      province: null,
      district: null,
      ward: null
    }
  });

  const province = watch("province");
  const district = watch("district");

  React.useEffect(() => {
    if (!customerInfoId) return;
    CustomerService.findById(customerInfoId)
      .then((response) => {
        reset({
          customerFullName: response.customerFullName || '',
          customerPhone: response.customerPhone || '',
          customerEmail: response.customerEmail || '',
          dob: response.dob || null,
          male: response.male || true,
          customerAddress: response.customerAddress || '',
          province: response.province || null,
          district: response.district || null,
          ward: response.ward || null
        })
      })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const pullProvinces = () => {
    FilterService
      .getCities(1)
      .then(cities => setProvinces(cities.map(city => ({ value: city.areaDataId, text: city.areaDataName }))));
  }

  const pullDistricts = (city) => {
    FilterService
      .getDistricts(city)
      .then(districts => setDistricts(districts.map(district => ({ value: district.areaDataId, text: district.areaDataName }))));
  }

  const pullWards = (district) => {
    FilterService
      .getWards(district)
      .then(wards => setWards(wards.map(ward => ({ value: ward.areaDataId, text: ward.areaDataName }))));
  }

  // After Component render for the first time,
  // Get list of `provinces`, `districts` and `wards` bases on current `province`, `district` and `ward`.
  // This help showing correct label in the select tags
  // If do not call this, label in the select tags just are the **numbers** that indicate `province`, `district`, and `ward`.
  React.useEffect(() => {
    pullProvinces();
    const province = parseInt(userData.province);
    if (province) pullDistricts(province);
    const district = parseInt(userData.district);
    if (district) pullWards(district);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  React.useEffect(() => {
    if (!province) return;
    setValue('district', null);
    setValue('ward', null);
    setDistricts([]); setWards([]);
    pullDistricts(parseInt(province));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [province]);

  React.useEffect(() => {
    if (!district) return;
    setValue('ward', null);
    setWards([]);
    pullDistricts(parseInt(province));
    pullWards(parseInt(district));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [district]);

  return (
    <Form onSubmit={handleSubmit(onSubmit)}>
      <h1 className={styles.titleText}>{titleText}</h1>
      <Row>
        <Col />
        <Col xs="auto">
          <Row style={{ gap: "10px", padding: "0 10px" }}>
            <button className={styles.btnWhite} onClick={e => {
              e.preventDefault();
              history.goBack();
            }}>{intl.formatMessage({ id: 'cancel' })}</button>
            <button className={styles.btnYellow}>{submitText}</button>
          </Row>
        </Col>
      </Row>
      <h3 className={styles.headingText}>Thông tin chung</h3>
      <Card className='px-1' style={{ borderRadius: "18px" }}>
        <CardBody>
          <div className='d-flex flex-column' style={{ gap: "10px" }}>
            <Row className='align-items-center'>
              <Col className='col-12'>
                <Row className='align-items-center'>
                  <Col className='col-12 col-sm-4 col-lg-2'>
                    <Row className='align-items-center px-1' style={{ color: '#544CA6' }}>
                      <ReactFeather.User />
                      <span className='ml-1'>{intl.formatMessage({ id: 'Full name' })}</span>
                      <span style={{ color: 'red' }}>&nbsp;*</span>
                    </Row>
                  </Col>
                  <Col className='my-2'>
                    <Controller
                      name="customerFullName"
                      control={control}
                      render={({ field }) => <Fragment>
                        <Input
                          {...field}
                          type="text"
                          placeholder={intl.formatMessage({ id: 'Enter customer full name' })}
                          invalid={!!errors.customerFullName}
                          maxLength={100}
                          bsSize="lg"
                        />
                        <FormFeedback style={{ position: 'absolute' }}>{errors.customerFullName?.message}</FormFeedback>
                      </Fragment>}
                      rules={{ required: intl.formatMessage({ id: 'Please enter customer full name' }) }}
                    />
                  </Col>
                </Row>
              </Col>
              <Col className='col-12 col-lg-6'>
                <Row className='align-items-center'>
                  <Col className='col-12 col-sm-4 col-lg-4'>
                    <Row className='align-items-center px-1' style={{ color: '#544CA6' }}>
                      <ReactFeather.Phone />
                      <span className='ml-1'>{intl.formatMessage({ id: 'Phone number' })}</span>
                      <span style={{ color: 'red' }}>&nbsp;*</span>
                    </Row>
                  </Col>
                  <Col className='my-2'>
                    <Controller
                      name="customerPhone"
                      control={control}
                      render={({ field }) => <Fragment>
                        <Input
                          {...field}
                          type="text"
                          placeholder={intl.formatMessage({ id: 'Enter customer phone' })}
                          invalid={!!errors.customerPhone}
                          maxLength={20}
                          bsSize="lg"
                        />
                        <FormFeedback style={{ position: 'absolute' }}>{errors.customerPhone?.message}</FormFeedback>
                      </Fragment>}
                      rules={{
                        required: intl.formatMessage({ id: 'Please enter customer phone' }),
                        pattern: {
                          value: /^\d+$/,
                          message: intl.formatMessage({ id: 'Invalid phone number' }) // JS only: <p>error message</p> TS only support string
                        }
                      }}
                    />
                  </Col>
                </Row>
              </Col>
              <Col className='col-12 col-lg-6'>
                <Row className='align-items-center'>
                  <Col className='col-12 col-sm-4 col-lg-auto'>
                    <Row className='align-items-center px-1' style={{ color: '#544CA6' }}>
                      <ReactFeather.Mail />
                      <span className='ml-1'>Email</span>
                    </Row>
                  </Col>
                  <Col className='my-2'>
                    <Controller
                      name="customerEmail"
                      control={control}
                      render={({ field }) => <Fragment>
                        <Input
                          {...field}
                          type="text"
                          placeholder={intl.formatMessage({ id: 'Enter customer email' })}
                          invalid={!!errors.customerEmail}
                          maxLength={100}
                          bsSize="lg"
                        />
                        <FormFeedback style={{ position: 'absolute' }}>{errors.customerEmail?.message}</FormFeedback>
                      </Fragment>}
                      rules={{
                        validate: email => {
                          if (email === '') return;
                          if (!validateEmail(email)) return intl.formatMessage({ id: 'Invalid email address' });
                        }
                      }}
                    />
                  </Col>
                </Row>
              </Col>
              <Col className='col-12'>
                <Row className='align-items-center'>
                  <Col className='col-12 col-sm-4 col-lg-2'>
                    <Row className='align-items-center px-1' style={{ color: '#544CA6' }}>
                      <BirthdayCake />
                      <span className='ml-1'>{intl.formatMessage({ id: 'DOB' })}</span>
                    </Row>
                  </Col>
                  <Col className='my-2'>
                    <Controller
                      name="dob"
                      control={control}
                      render={({ field }) => <Fragment>
                        <DatePicker
                          {...field}
                          className={classNames('form-control', {
                            'is-invalid': errors.dob
                          })}
                          style={{ borderRadius: "0.357rem" }}
                          format={['DD/MM/YYYY', 'DD/MM/YY']}
                          placeholder='dd/mm/yyyy'
                        />
                        <FormFeedback style={{ position: 'absolute' }}>{errors.dob?.message}</FormFeedback>
                      </Fragment>}
                    />
                  </Col>
                </Row>
              </Col>
              <Col className='col-12'>
                <Row className='align-items-center'>
                  <Col className='col-12 col-sm-4 col-lg-2'>
                    <Row className='align-items-center px-1' style={{ color: '#544CA6' }}>
                      <GenderIcon />
                      <span className='ml-1'>{intl.formatMessage({ id: 'Gender' })}</span>
                    </Row>
                  </Col>
                  <Col className='my-2'>
                    <Controller
                      name="male"
                      control={control}
                      render={({ field: { onChange, value } }) => <Fragment>
                        <Switch
                          onChange={onChange}
                          checked={value}
                        />
                        <span className='ml-1'>{value ? intl.formatMessage({ id: 'Male' }) : intl.formatMessage({ id: 'Female' })}</span>
                      </Fragment>}
                    />
                  </Col>
                </Row>
              </Col>
              <Col className='col-12'>
                <Row className='align-items-center'>
                  <Fragment>
                    <Col className='col-12 col-sm-4 col-lg-2'>
                      <Row className='align-items-center px-1' style={{ color: '#544CA6' }}>
                        <ReactFeather.MapPin />
                        <span className='ml-1'>{intl.formatMessage({ id: 'address' })}</span>
                      </Row>
                    </Col>
                    <Col className='my-2'>
                      <Controller
                        name="customerAddress"
                        control={control}
                        render={({ field }) => <Fragment>
                          <Input
                            {...field}
                            type="text"
                            placeholder={intl.formatMessage({ id: 'Enter address' })}
                            maxLength={255}
                            bsSize="lg"
                          />
                        </Fragment>}
                      />
                    </Col>
                  </Fragment>
                </Row>
              </Col>
              <Col className='col-12'>
                <Row className='align-items-center'>
                  <Fragment>
                    <Col className='col-12 col-sm-4 col-lg-2'>
                      <Row className='align-items-center px-1' style={{ color: '#544CA6' }}>
                      </Row>
                    </Col>
                    <Col className='my-2'>
                      <Row style={{ gap: "10px" }}>
                        <Col>
                          <Controller
                            name="province"
                            control={control}
                            render={({ field }) => <Fragment>
                              <Select
                                {...field}
                                showSearch
                                showArrow={false}
                                placeholder={<>--{intl.formatMessage({ id: 'Select province' })}<span style={{ color: 'red' }}>&nbsp;*&nbsp;</span>--</>}
                                className={classNames('form-control', {
                                  'is-invalid': errors.province
                                })}
                                bordered={false}
                                style={{ paddingTop: 2 }}
                                options={provinces.map(({ value, text }) => ({ value: value.toString(), label: text }))}
                                filterOption={(input, option) => option.label.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                              >
                              </Select>
                              <FormFeedback style={{ position: 'absolute' }}>{errors.province?.message}</FormFeedback>
                            </Fragment>}
                            rules={{ required: intl.formatMessage({ id: 'Please select province' }) }}
                          />
                        </Col>
                        <Col>
                          <Controller
                            name="district"
                            control={control}
                            render={({ field }) => <Fragment>
                              <Select
                                {...field}
                                showSearch
                                showArrow={false}
                                placeholder={<>--{intl.formatMessage({ id: 'Select district' })}<span style={{ color: 'red' }}>&nbsp;*&nbsp;</span>--</>}
                                className={classNames('form-control', {
                                  'is-invalid': errors.district
                                })}
                                bordered={false}
                                style={{ paddingTop: 2 }}
                                options={districts.map(({ value, text }) => ({ value: value.toString(), label: text }))}
                                filterOption={(input, option) => option.label.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                              >
                              </Select>
                              <FormFeedback style={{ position: 'absolute' }}>{errors.district?.message}</FormFeedback>
                            </Fragment>}
                            rules={{ required: intl.formatMessage({ id: 'Please select district' }) }}
                          />
                        </Col>
                        <Col>
                          <Controller
                            name="ward"
                            control={control}
                            render={({ field }) => <Fragment>
                              <Select
                                {...field}
                                showSearch
                                showArrow={false}
                                placeholder={<>--{intl.formatMessage({ id: 'Select ward' })}<span style={{ color: 'red' }}>&nbsp;*&nbsp;</span>--</>}
                                className={classNames('form-control', {
                                  'is-invalid': errors.ward
                                })}
                                bordered={false}
                                style={{ paddingTop: 2 }}
                                options={wards.map(({ value, text }) => ({ value: value.toString(), label: text }))}
                                filterOption={(input, option) => option.label.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                              >
                              </Select>
                              <FormFeedback style={{ position: 'absolute' }}>{errors.ward?.message}</FormFeedback>
                            </Fragment>}
                            rules={{ required: intl.formatMessage({ id: 'Please select ward' }) }}
                          />
                        </Col>
                      </Row>
                    </Col>
                  </Fragment>
                </Row>
              </Col>
            </Row>
          </div>
        </CardBody>
      </Card>
      <h3 className={styles.headingText}>Nhu cầu</h3>
      <Input type="textarea" name="text" />
    </Form>
  )
}

export default injectIntl(memo(CustomerForm))