import requestService from './request';
import { removeEmptyProperties } from '@src/helper/object';
import { ENDPOINTS } from '@src/constants/endpoints';
import { notification } from "antd";

export default class CustomerService {

  static async deleteById(id) {
    const data = { id }
    const response = await requestService
      .send({
        method: "POST",
        path: ENDPOINTS.CUSTOMER_DELETE_BY_ID,
        data: data
      })
      .then(res => !res?.error && res?.data)
      .catch((err) => {
        notification.error({ message: err.message });
      });
    return response;
  }

  static async find(searchText) {
    const data = { searchText }
    const response = await requestService
      .send({
        method: "POST",
        path: ENDPOINTS.CUSTOMER_FIND,
        data: removeEmptyProperties(data)
      })
      .then(res => !res?.error && res?.data?.data)
      .catch((err) => {
        notification.error({ message: err.message });
      });
    return response;
  }

  static async findById(id) {
    const response = await requestService
      .send({
        method: "POST",
        path: ENDPOINTS.CUSTOMER_FIND_BY_ID,
        data: {id}
      })
      .then(res => !res?.error && res?.data)
      .catch((err) => {
        notification.error({ message: err.message });
      });
    return response;
  }

  static async insert(data) {
    const response = await requestService
      .send({
        method: "POST",
        path: ENDPOINTS.CUSTOMER_INSERT,
        data: data
      })
      .then(res => !res?.error && res?.data)
      .catch((err) => {
        notification.error({ message: err.message });
      });
    return response;
  }

  static async updateById(id, data) {
    const response = await requestService
      .send({
        method: "POST",
        path: ENDPOINTS.CUSTOMER_UPDATE_BY_ID,
        data: {id, data}
      })
      .then(res => !res?.error && res?.data)
      .catch((err) => {
        notification.error({ message: err.message });
      });
    return response;
  }
}