import React, { memo } from 'react';

import { useHistory } from 'react-router-dom'

import { notification } from "antd";

import CustomerForm from './components/CustomerForm';

import CustomerService from '@src/services/customerInfo';

import { injectIntl } from 'react-intl';

import {
   DELAY_MILLISECONDS_BEFORE_GO_BACK
} from './constant/constant';

function EditCustomer({
   intl,
   customerInfoId
}) {
   const [userData, setUserData] = React.useState({});
   const history = useHistory();

   const onSubmit = (userData) => {
      const {
         customerPhone,
         customerEmail,
         customerAddress,
         customerFullName,
      } = userData;
      CustomerService.updateById(customerInfoId, {
         customerPhone,
         customerEmail,
         customerAddress,
         customerFullName
      })
         .then(() => {
            notification.success({ message: intl.formatMessage({ id: 'updateCustomerSuccessfully' }) });
            setTimeout(() => {
               history.goBack();
            }, DELAY_MILLISECONDS_BEFORE_GO_BACK);
         })
   }

   return (
      <div className='p-1'>
         <CustomerForm
            customerInfoId={customerInfoId}
            userData={userData}
            setUserData={setUserData}
            onSubmit={onSubmit}
            titleText={intl.formatMessage({ id: 'updateCustomer' })}
            submitText={intl.formatMessage({ id: 'Update' })}
         />
      </div>
   )
}
export default injectIntl(memo(EditCustomer))