import React, { Fragment, useCallback } from 'react';

import { useHistory } from 'react-router-dom'

import {
  Card, Input, Row, Col, CardHeader, CardBody,
  UncontrolledDropdown, DropdownMenu, DropdownItem, DropdownToggle
} from 'reactstrap'

import DataTable from 'react-data-table-component'

import CustomPagination from "@components/customPaginationV1";

import { MoreVertical, Edit, Trash2, Plus } from 'react-feather'

import { ReactComponent as UserIcon } from "@src/assets/images/icons/user.svg";
import { ReactComponent as SearchIcon } from "@src/assets/images/icons/search.svg";
import { ReactComponent as FilterIcon } from "@src/assets/images/icons/filter.svg";

import CustomerService from '@src/services/customerInfo';

import { debounced } from '@src/helper/common';

import Swal from 'sweetalert2';

import { injectIntl } from 'react-intl';

import { useForm, Controller } from 'react-hook-form';

import 'sweetalert2/src/sweetalert2.scss'

import {
  PAGINATION_ROWS_PER_PAGE,
  DEBOUNCE_MILLISECONDS
} from './constant/constant';

import * as styles from './styles/ListCustomer.module.scss'

function ListCustomer({ intl }) {
  const [AllItems, setAllItems] = React.useState([]);
  const [showItems, setShowItems] = React.useState([]);
  const [currentPage, setCurrentPage] = React.useState(0);
  const [totalPages, setTotalPages] = React.useState(0);

  const history = useHistory();

  const { control } = useForm({
    defaultValues: {
      searchText: '',
    }
  });

  React.useEffect(() => {
    setTotalPages(parseInt(Math.ceil(AllItems.length / PAGINATION_ROWS_PER_PAGE)));
  }, [AllItems])

  React.useEffect(() => {
    const start = currentPage * PAGINATION_ROWS_PER_PAGE;
    setShowItems(AllItems.slice(start, start + PAGINATION_ROWS_PER_PAGE));
  }, [currentPage, AllItems]);

  const fetchCustomers = (searchText = '') => {
    CustomerService.find(searchText).then((customers) => setAllItems(customers))
  }

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const debounceSearch = useCallback(
    debounced(DEBOUNCE_MILLISECONDS, (e) => {
      CustomerService.find(e.target.value).then((customers) => setAllItems(customers))
      fetchCustomers(e.target.value)
    }),
    [],
  );

  // fetch customers for the first time
  React.useEffect(fetchCustomers, []);

  const handleDelete = (e, customerInfoId) => {
    e.preventDefault();
    Swal.fire({
      title: intl.formatMessage({ id: 'deleteCustomer' }) + '?',
      text: intl.formatMessage({ id: 'Are you sure you want to delete this customer' }) + '?',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: intl.formatMessage({ id: 'delete' }),
      cancelButtonText: intl.formatMessage({ id: 'cancel' })
    }).then((result) => {
      if (result.isConfirmed) {
        CustomerService.deleteById(customerInfoId)
          .then(() => {
            setAllItems(AllItems.filter(item => item.customerInfoId !== customerInfoId))
            Swal.fire(
              intl.formatMessage({ id: 'deleted' }) + '!',
              intl.formatMessage({ id: 'Deleted customer successfully' }),
              'success'
            )
          })
      }
    })
  }

  const CustomerColumns = [
    {
      id: 'id',
      name: 'ID',
      width: '80px',
      cell: ({ customerInfoId }) => {
        return (
          <div style={{ overflow: "hidden" }}>
            <a href={`/customers/${customerInfoId}/edit`}
              style={{ display: 'block', width: '500px' }}
              onClick={e => {
                e.preventDefault();
                history.push('/dashboard/PagesSidebar/Customer', {
                  tab: 'edit',
                  customerInfoId
                })
              }
              }>#{customerInfoId}</a>
          </div>
        )
      },
    },
    {
      id: 'fullName',
      name: intl.formatMessage({ id: 'Full name' }),
      maxWidth: '250px',
      selector: 'customerFullName',
    },
    {
      id: 'email',
      name: 'Email',
      maxWidth: '400px',
      cell: ({ customerEmail }) => <a href={`mailto:${customerEmail}`}>{customerEmail}</a>,
    },
    {
      id: 'phoneNumber',
      name: intl.formatMessage({ id: 'Phone number' }),
      width: '150px',
      cell: ({ customerPhone }) => <a href={`tel:${customerPhone}`} className="text-dark">{customerPhone}</a>,
    },
    {
      id: 'address',
      name: intl.formatMessage({ id: 'Address' }),
      minWidth: '200px',
      selector: 'customerAddress',
    },
    {
      id: 'action',
      name: '',
      width: "50px",
      cell: ({ customerInfoId }) => {
        return (
          <div>
            <UncontrolledDropdown>
              <DropdownToggle className='p-0' color='transparent' size='sm'>
                <MoreVertical className='m-0 p-0' />
              </DropdownToggle>
              <DropdownMenu>
                <DropdownItem onClick={e => {
                  e.preventDefault();
                  history.push('/dashboard/PagesSidebar/Customer', {
                    tab: 'edit',
                    customerInfoId
                  })
                }} style={{ width: "100%" }}>
                  <Edit className='mr-3' size={15} />
                  <span className='align-middle'>
                    {intl.formatMessage({ id: 'edit' })}
                  </span>
                </DropdownItem>
                <DropdownItem onClick={(e) => handleDelete(e, customerInfoId)} style={{ width: "100%" }}>
                  <Trash2 className='mr-3' size={15} />
                  <span className='align-middle'>
                    {intl.formatMessage({ id: 'delete' })}
                  </span>
                </DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown>
          </div>
        )
      },
      style: {
        justifyContent: 'center'
      }
    },
  ]

  const customStyles = {
    headRow: {
      style: {
        backgroundColor: '#36CBCB4D',
        paddingLeft: '15px',
        paddingRight: '15px',
        minHeight: '35px'
      },
    },
    headCells: {
      style: {
        color: 'black',
        textTransform: 'uppercase',
        paddingLeft: '14px',
        paddingRight: '14px',
        fontWeight: 'bold',
      }
    },
    rows: {
      style: {
        minHeight: '88px',
        paddingLeft: '14px',
        paddingRight: '14px',
        display: 'flex',
        alignItems: 'center'
      }
    },
    cells: {
      style: (row) => ({
        padding: '14px',
        borderLeft: ['id', 'action'].includes(row.column.id) ? '' : '2px dotted rgba(0, 0, 0, 0.15)',
        height: "40px",
      })
    },
  };

  return (
    <Card className={styles.card}>
      <CardHeader className={styles.cardHeader}>
        <Col md={6}>
          <Row className={styles.rowHeaderLeft}>
            <UserIcon />
            <div className={styles.listCustomerText}>{intl.formatMessage({ id: 'List of Customers' })}</div>
            <div className={styles.totalText}>{intl.formatMessage({ id: 'XTotal' }, { x: AllItems.length })}</div>
          </Row>
        </Col>
        <Col md={6} className={styles.colHeaderRight}>
          <Row className={styles.rowHeaderRight}>
            <div></div>
            <button className={styles.btnAdd}
              onClick={(e) => {
                e.preventDefault();
                history.push('/dashboard/PagesSidebar/Customer', {
                  tab: 'create'
                })
              }}
            >
              <Plus size={15} />
              <div>{intl.formatMessage({ id: 'addNew' })}</div>
            </button>
            <div className={styles.searchContainer}>
              <SearchIcon />
              <Controller
                name="searchText"
                control={control}
                render={({ field: { onChange, ...field } }) => <Fragment>
                  <Input
                    {...field}
                    placeholder={intl.formatMessage({ id: 'Enter keywords' })}
                    maxLength={100}
                    className={styles.inputSearch}
                    onChange={(e) => {
                      // setValue to input
                      onChange(e)
                      // fetch customers if user stop typing for DEBOUNCE_MILLISECONDS
                      debounceSearch(e)
                    }}
                  />
                </Fragment>}
              />
            </div>
            <button className={styles.btnFilter}>
              <FilterIcon />
              <div>{intl.formatMessage({ id: 'Filter' })} (2)</div>
            </button>
          </Row>
        </Col>
      </CardHeader>
      <CardBody className={styles.cardBody}>
        <DataTable
          noHeader
          columns={CustomerColumns}
          data={showItems}
          customStyles={customStyles}
        />
        <CustomPagination
          totalPages={totalPages}
          currentPage={currentPage}
          setCurrentPage={setCurrentPage}
        />
      </CardBody>
    </Card>
  )
}

export default injectIntl(React.memo(ListCustomer))