import React, { memo } from 'react';

import { useHistory } from 'react-router-dom'

import { notification } from "antd";

import CustomerForm from './components/CustomerForm';

import CustomerService from '@src/services/customerInfo';

import { injectIntl } from 'react-intl';

import {
   DELAY_MILLISECONDS_BEFORE_GO_BACK
} from './constant/constant';

function CreateCustomer({ intl }) {
   const [userData] = React.useState({});
   const history = useHistory();

   const onSubmit = (userData) => {
      const {
         customerPhone,
         customerEmail,
         customerAddress,
         customerFullName,
      } = userData;

      CustomerService.insert({
         customerPhone,
         customerEmail,
         customerAddress,
         customerFullName,
      })
         .then(() => {
            notification.success({ message: intl.formatMessage({ id: 'createCustomerSuccessfully' }) });
            setTimeout(() => {
               history.goBack();
            }, DELAY_MILLISECONDS_BEFORE_GO_BACK);
         })
   }

   return (
      <div className='p-1'>
         <CustomerForm
            userData={userData}
            onSubmit={onSubmit}
            titleText={intl.formatMessage({ id: 'addNewCustomer' })}
            submitText={intl.formatMessage({ id: 'addNew' })}
         />
      </div>
   )
}
export default injectIntl(memo(CreateCustomer))